.form-box{

    width: 90%;
    max-width: 450px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 25px;
    background: #c49696;
    padding: 70px 60px; 
    text-align: center;

}


.form-box h1{
    font-size: 30px;
    margin-bottom: 60px;
    color: #680a0a;
    position: relative;

}

.form-box h1::after{

    content: '';
    width: 30px;
    height: 4px;
    border-radius: 3px;
    background : #680a0a;
    position: absolute;
    bottom: -12px;
    left: 50%;
    transform: translateX(-50%);
}

.input-field{

    background: #680a0a;
    margin: 10px;
    border-radius: 3px;
    display: flex;
    align-items: center;
    max-height: 65px;
    transition: max-height 0.5s;
    overflow: hidden;

}

input{

    width: 100%;
    background: transparent;
    border: 0;
    outline: 0;
    padding: 18px 15px;

}

.input-field i{
    margin-left: 15px;
    color: #999;
}

form p {
    text-align: left;
    font-size: 12px;
    
}

form p a{

    text-decoration:  none;
    color: #680a0a;

}

.btn-field{

    width: 100%;
    display: flex;
    justify-content: space-between;
}

.btn-field button{

    flex-basis: 48%;
    background: #680a0a;
    color: #fff;
    height: 40px;
    border-radius: 20px;
    border: 0;
    outline: 0;
    cursor: pointer;
    transition: background 1s;

}

.input-group{

    height: 280px;

}

.btn-field button.disable{

    background: #333;
    color: #555;
}